import Modal from "react-bootstrap/Modal";
import React, {useMemo, useState} from "react";
import DataTable from 'react-data-table-component';
import FilterComponent from "../../agents/FilterComponent";
import {customStyles} from "../../../../utils/Common";

const TableFlightsData = (props) => {
  const columns = [
    {
      name: "Id",
      selector: row => row.id,
      sortable: true,
      width: '80px'
    },
    {
      name: "Booking Id",
      selector: row => row.bookId,
      sortable: true,
      width: '125px'
    },
    {
      name: "Flight No.",
      selector: row => row.flightNo,
      sortable: true,
      width: '120px'
    },
    {
      name: "Agency Name",
      selector: row => row.agencyName,
      sortable: true,
      width: '140px'
    },
    {
      name: "Airline",
      selector: row => row.airline,
      sortable: true,
      width: '100px'
    },
    {
      name: "Ticketing Status",
      selector: row => row.ticketingStatus,
      sortable: true,
      width: '160px'
    },
    {
      name: "Passenger Name",
      selector: row => row.passengerName,
      sortable: true,
      width: '160px'
    },
    {
      name: "Passenger Type",
      selector: row => row.passengerType,
      sortable: true,
      // width: '160px'
    },
    {
      name: "Sector",
      selector: row => row.sector,
      sortable: true,
      width: '150px'
    },
    {
      name: "Departure Date Time",
      selector: row => row.departureDate + ' ' + row.departureTime,
      sortable: true,
      width: '190px'
    },
    {
      name: "Arrival Date Time",
      selector: row => row.arrivalDate + ' ' + row.arrivalTime,
      sortable: true,
      width: '190px'
    },
    {
      name: "Booking DateTime",
      selector: row => row[""],
      sortable: true,
      width: '200px'
    },
  ];

  const [filterText, setFilterText] = React.useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);

  const filteredItems = props.data.filter(
    item => JSON.stringify(item).toLowerCase().indexOf(filterText.toLowerCase()) !== -1
  );

  const subHeaderComponent = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <>
        <FilterComponent onFilter={e => setFilterText(e.target.value)} onClear={handleClear} filterText={filterText}/>
      </>
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <DataTable
        columns={columns}
        data={filteredItems}
        striped
        pagination
        subHeader
        subHeaderComponent={subHeaderComponent}
        customStyles={customStyles}
      />
    </>
  )
}
export default TableFlightsData;
