import React, {useMemo, useState} from "react";
import DataTable from 'react-data-table-component';
import FilterComponent from "../../agents/FilterComponent";
import {customStyles} from "../../../../utils/Common";

const TableFlightsData = (props) => {
  const columns = [
    {
      name: "Details",
      button: true,
      cell: row => <>
        <button onClick={() => props.click(row.id)} className="btn btn-link">
          <i className="fa fa-eye"></i>
        </button>
      </>,
      width: '70px'
    },
    {
      name: "Booking Id",
      selector: row => row.id,
      sortable: true,
      width: '130px'
    },
    {
      name: "Airline",
      button: true,
      cell: row => <>
        <img src={`https://content.airhex.com/content/logos/airlines_${row.airlineCode}_60_50_s.png`} alt=""/>
      </>,
      width: '60px'
    },
    {
      name: "Supplier",
      selector: row => row.supplier,
      sortable: true,
      width: '110px'
    },
    {
      name: "PNR",
      selector: row => row.pnr,
      sortable: true,
      // width: '130px'
    },
    {
      name: "Ticket Id",
      selector: row => row.ticketId,
      sortable: true,
      width: '130px'
    },
    {
      name: "Agent",
      selector: row => row.agentEmail,
      sortable: true,
      width: '220px'
    },
    {
      name: "Booking Date",
      selector: row => row.bookingDT,
      sortable: true,
      width: '170px'
    },
    {
      name: "Booking Status",
      selector: row => <b>{row.bookingStatus}</b>,
      sortable: true,
      width: '150px'
    },
    {
      name: "Payment Status",
      selector: row => <b>{row.paymentStatus}</b>,
      sortable: true,
      width: '160px'
    },
    {
      name: "Payment Type",
      selector: row => <b className="text-capitalize">{row.paymentType}</b>,
      sortable: true,
      width: '160px'
    },
    {
      name: "Total Adult",
      selector: row => row.totalAdult,
      sortable: true,
      width: '130px'
    },
    {
      name: "Total Child",
      selector: row => row.totalChild,
      sortable: true,
      width: '130px'
    },
    {
      name: "Total Infant",
      selector: row => row.totalInfant,
      sortable: true,
      width: '130px'
    },
  ];


  const [filterText, setFilterText] = React.useState("");
  const [resetPaginationToggle, setResetPaginationToggle] = React.useState(false);

  const filteredItems = props.data.filter(
    item => JSON.stringify(item).toLowerCase().indexOf(filterText.toLowerCase()) !== -1
  );

  const subHeaderComponent = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText("");
      }
    };

    return (
      <>
        <FilterComponent onFilter={e => setFilterText(e.target.value)} onClear={handleClear} filterText={filterText}/>
      </>
    );
  }, [filterText, resetPaginationToggle]);

  return (
    <>
      <DataTable
        columns={columns}
        data={filteredItems}
        striped
        pagination
        subHeader
        subHeaderComponent={subHeaderComponent}
        customStyles={customStyles}
      />
    </>
  )
}
export default TableFlightsData;
